<template>
  <div class="max-w-sm mx-auto pt-16">
    <h2>Login in you in ...</h2>
    <pre>{{ authRequest }}</pre>
  </div>
</template>
<script>
export default {
  computed: {
    authRequest() {
        let {acc,token} =this.$route.query;
        return {email: acc, magic:token, strategy: "link"}
    }
  },
  async mounted(){
      try{
          await this.$feathers.reAuthenticate();
          this.$router.push("/visa");
      }catch (error){
          this.$feathers.authenticate(this.authRequest)
          .then(()=> this.$router.push("/visa"))
          .catch(error=> console.error(error))
      }
  }
};
</script>
